"use client";
import Link from "next/link";
import { useState, useEffect } from "react";

const NavMenu = ({ pathname, categories = [], isMobile = false, onLinkClick }) => {
  const [openSubmenu, setOpenSubmenu] = useState(null);

  const allNavLinks = categories
    .filter((category) => category.ShowOnMenu && category.IsActive)
    .sort((a, b) => (a.MenuOrder || 999) - (b.MenuOrder || 999))
    .map((category) => ({
      label: category.CategoryName,
      href: `/category/${category.Slug}`,
      id: category.Id,
    }));

  const handleSubmenuToggle = (linkId) => {
    if (isMobile) {
      setOpenSubmenu(openSubmenu === linkId ? null : linkId);
    }
  };

  const handleLinkClick = () => {
    if (onLinkClick) {
      onLinkClick();
    }
    setOpenSubmenu(null);
  };

  // Desktop version
  if (!isMobile) {
    return (
      <nav className="flex space-x-8 lg:text-[20px] font-semibold items-center">
        {allNavLinks.map((link, idx) => {
          const isActive = pathname === link.href;

          if (link.submenu) {
            return (
              <div key={link.id || idx} className="relative group inline-block">
                <div className="dynamic-green-color font-semibold relative inline-block cursor-pointer">
                  <span className="dynamic-green-hover transition-colors duration-300">
                    {link.label}
                  </span>
                </div>
                <div className="absolute top-full -left-20 mt-0 w-[180px] opacity-0 scale-95 translate-y-2 pointer-events-none group-hover:opacity-100 group-hover:scale-100 group-hover:translate-y-0 group-hover:pointer-events-auto transition-all duration-300 ease-out rounded-xl border border-gray-200 bg-white/90 backdrop-blur-md shadow-2xl z-50">
                  {link.submenu.map((sublink, i) => {
                    const isSubActive = pathname === sublink.href;
                    return (
                      <Link
                        key={i}
                        href={sublink.href}
                        className={`relative block px-5 py-3 text-[17px] font-medium text-gray-800 tracking-wide dynamic-submenu-hover transition-all duration-200 rounded-md ${isSubActive
                            ? "dynamic-submenu-active font-semibold"
                            : ""
                          }`}
                      >
                        <span>{sublink.label}</span>
                      </Link>
                    );
                  })}
                </div>
              </div>
            );
          } else {
            return (
              <Link
                key={link.id || idx}
                href={link.href}
                className={`dynamic-green-color font-semibold relative inline-block dynamic-active-underline ${isActive ? "active-underline" : ""
                  }`}
              >
                <span>{link.label}</span>
                <span className="dynamic-underline-line"></span>
              </Link>
            );
          }
        })}
      </nav>
    );
  }

  // Mobile version
  return (
    <nav className="flex flex-col space-y-2 p-4">
      {allNavLinks.map((link, idx) => {
        const isActive = pathname === link.href;

        if (link.submenu) {
          return (
            <div key={link.id || idx} className="border-b border-gray-100 pb-2">
              <button
                onClick={() => handleSubmenuToggle(link.id || idx)}
                className="flex items-center justify-between w-full py-3 px-2 text-lg font-semibold dynamic-green-color text-left"
              >
                <span>{link.label}</span>
                <svg
                  className={`w-5 h-5 transition-transform duration-200 ${openSubmenu === (link.id || idx) ? "rotate-180" : ""
                    }`}
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M19 9l-7 7-7-7"
                  />
                </svg>
              </button>
              {openSubmenu === (link.id || idx) && (
                <div className="pl-4 space-y-1 mt-2">
                  {link.submenu.map((sublink, i) => {
                    const isSubActive = pathname === sublink.href;
                    return (
                      <Link
                        key={i}
                        href={sublink.href}
                        onClick={handleLinkClick}
                        className={`block py-2 px-3 text-base font-medium rounded-md transition-colors duration-200 ${isSubActive
                            ? "dynamic-submenu-active font-semibold"
                            : "text-gray-700 hover:bg-gray-50 dynamic-green-hover"
                          }`}
                      >
                        {sublink.label}
                      </Link>
                    );
                  })}
                </div>
              )}
            </div>
          );
        } else {
          return (
            <Link
              key={link.id || idx}
              href={link.href}
              onClick={handleLinkClick}
              className={`block py-3 px-2 text-lg font-semibold rounded-md transition-colors duration-200 ${isActive
                  ? "dynamic-green-color bg-gray-50"
                  : "dynamic-green-color hover:bg-gray-50"
                }`}
            >
              {link.label}
            </Link>
          );
        }
      })}
    </nav>
  );
};

export default NavMenu;
