"use client";
import { useState } from "react";
import TopBar from "./TopBar";
import NavMenu from "./NavMenu";
import Image from "next/image";
import Link from "next/link";
import {
  fetchHomeData,
  fetchHomeSettings,
  extractCategoriesFromHomeData,
} from "../../api/services/homeService";

const Header = async ({ currentPath }) => {
  const [homeData, homeSettings] = await Promise.all([
    fetchHomeData(),
    fetchHomeSettings(),
  ]);

  const categories = extractCategoriesFromHomeData(homeData);
  const logoUrl = homeSettings?.LogoUrl || "/logo.png";
  const primaryColor = homeSettings?.PrimaryColor || "#22c55e";
  const secondaryColor = homeSettings?.SecondaryColor || "#16a34a";

  return (
    <>
      <style dangerouslySetInnerHTML={{
        __html: `
          .header-container {
            --primary-color: ${primaryColor};
            --secondary-color: ${secondaryColor};
            --primary-color-20: ${primaryColor}33;
            --primary-color-40: ${primaryColor}66;
            --primary-color-80: ${primaryColor}CC;
          }
          .dynamic-green-bg {
            background-color: var(--primary-color);
          }
          .dynamic-green-color {
            color: var(--primary-color);
          }
          .dynamic-green-hover:hover {
            color: var(--secondary-color);
          }
          .dynamic-active-underline.active-underline .dynamic-underline-line {
            width: 100%;
            background-color: var(--secondary-color);
          }
          .dynamic-underline-line {
            position: absolute;
            bottom: -2px;
            left: 0;
            width: 0;
            height: 2px;
            background-color: var(--secondary-color);
            transition: width 0.3s ease;
          }
          .dynamic-green-color:hover .dynamic-underline-line {
            width: 100%;
          }
          .dynamic-submenu-hover:hover {
            background-color: var(--primary-color-20);
            color: var(--secondary-color);
          }
          .dynamic-submenu-active {
            color: var(--secondary-color);
            background-color: var(--primary-color-20);
          }
          
          /* Mobile menu animations */
          .mobile-menu-enter {
            opacity: 0;
            transform: translateX(-100%);
          }
          .mobile-menu-enter-active {
            opacity: 1;
            transform: translateX(0);
            transition: opacity 300ms ease-in-out, transform 300ms ease-in-out;
          }
          .mobile-menu-exit {
            opacity: 1;
            transform: translateX(0);
          }
          .mobile-menu-exit-active {
            opacity: 0;
            transform: translateX(-100%);
            transition: opacity 300ms ease-in-out, transform 300ms ease-in-out;
          }
          
          /* Overlay animations */
          .overlay-enter {
            opacity: 0;
          }
          .overlay-enter-active {
            opacity: 1;
            transition: opacity 300ms ease-in-out;
          }
          .overlay-exit {
            opacity: 1;
          }
          .overlay-exit-active {
            opacity: 0;
            transition: opacity 300ms ease-in-out;
          }
        `
      }} />

      <HeaderContent
        currentPath={currentPath}
        categories={categories}
        logoUrl={logoUrl}
        homeSettings={homeSettings}
      />
    </>
  );
};

const HeaderContent = ({ currentPath, categories, logoUrl, homeSettings }) => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <header className="header-container border-b border-gray-200 shadow relative">
      <div className="lg:block hidden">
        <TopBar homeSettings={homeSettings} />
      </div>

      <div className="flex main_width items-center justify-between py-2 px-4 lg:px-0">
        <Link href="/" onClick={closeMobileMenu}>
          <div className="relative lg:w-[220px] md:w-[180px] w-[150px] md:h-[90px] h-[80px]">
            <Image
              src={logoUrl}
              alt="Ground Connect Logo"
              fill
              sizes="(max-width: 768px) 150px, (max-width: 1024px) 180px, 220px"
              className="w-full h-full object-cover"
              priority
            />
          </div>
        </Link>

        {/* Desktop Navigation */}
        <div className="lg:block hidden">
          <NavMenu pathname={currentPath} categories={categories} />
        </div>

        {/* Mobile Hamburger Button */}
        <button
          onClick={toggleMobileMenu}
          className="lg:hidden flex flex-col items-center justify-center w-8 h-8 space-y-1.5 focus:outline-none"
          aria-label="Toggle mobile menu"
        >
          <span
            className={`block w-6 h-0.5 bg-gray-600 transition-all duration-300 ${isMobileMenuOpen ? "rotate-45 translate-y-2" : ""
              }`}
          />
          <span
            className={`block w-6 h-0.5 bg-gray-600 transition-all duration-300 ${isMobileMenuOpen ? "opacity-0" : ""
              }`}
          />
          <span
            className={`block w-6 h-0.5 bg-gray-600 transition-all duration-300 ${isMobileMenuOpen ? "-rotate-45 -translate-y-2" : ""
              }`}
          />
        </button>
      </div>

      {/* Mobile Menu Overlay */}
      {isMobileMenuOpen && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={closeMobileMenu}
        />
      )}

      {/* Mobile Menu */}
      <div
        className={`fixed top-0 left-0 h-full w-80 max-w-[85vw] bg-white shadow-xl z-50 transform transition-transform duration-300 ease-in-out lg:hidden ${isMobileMenuOpen ? "translate-x-0" : "-translate-x-full"
          }`}
      >
        <div className="flex items-center justify-between p-4 border-b border-gray-200">
          <Link href="/" onClick={closeMobileMenu}>
            <div className="relative w-[120px] h-[60px]">
              <Image
                src={logoUrl}
                alt="Ground Connect Logo"
                fill
                sizes="120px"
                className="w-full h-full object-cover"
                priority
              />
            </div>
          </Link>
          <button
            onClick={closeMobileMenu}
            className="w-8 h-8 flex items-center justify-center focus:outline-none"
            aria-label="Close mobile menu"
          >
            <svg
              className="w-6 h-6 text-gray-600"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>

        <div className="overflow-y-auto h-full pb-20">
          <NavMenu
            pathname={currentPath}
            categories={categories}
            isMobile={true}
            onLinkClick={closeMobileMenu}
          />
        </div>
      </div>
    </header>
  );
};

export default Header;
